.navigation__link, .tag:hover, .logo-text {
  transition: all 0.3s ease-out;
}

.button, body {
  font-family: "Open Sans", Verdana, sans-serif;
}

.promo__text, .link-button, h1 {
  font-family: "Poppins", Verdana, sans-serif;
}

html {
  font-size: 10px;
}

body {
  font-style: normal;
  font-weight: normal;
  font-size: 1.6rem;
  line-height: 2.6rem;
  /*font-family: "Poppins", sans-serif;*/
}

.strategy__image {
  width: 150px;
}

h1 {
  font-style: normal;
  font-weight: bold;
  font-size: 4.4rem;
  line-height: 5.4rem;
  padding-top: 113px;
}

h2 {
  font-style: normal;
  font-weight: bold;
  font-size: 3.4rem;
  line-height: 4.6rem;
}

h3 {
  font-style: normal;
  font-weight: bold;
  font-size: 2.4rem;
  line-height: 3.4rem;
}

h4 {
  font-style: normal;
  font-weight: 600;
  font-size: 2.2rem;
  line-height: 3rem;
}

h5 {
  font-style: normal;
  font-weight: bold;
  font-size: 1.6rem;
  line-height: 2.6rem;
}

.block-shadowed {
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
}

.contacts-link {
  color: #FF9900;
  text-decoration: none;
}

.button {
  font-style: normal;
  font-weight: 600;
  font-size: 1.6rem;
  line-height: 2.2rem;
  padding: 11px 25px 13px;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s;
}
.button_bordered {
  background: #ffffff;
  border: 1px solid #D7D7D7;
  color: #544837;
  margin: 0 10px 0 0;
}
.button_bordered:hover {
  background: #BCD0E5;
  border-color: #BCD0E5;
}
.button_colored {
  background: #FF9900;
  border: 1px solid #FF9900;
  color: #ffffff;
}
.button_colored:hover {
  background: #FF7549;
  border-color: #FF7549;
}

.link-button {
  color: #FF9900;
  display: inline-flex;
  align-items: center;
}

.link-button__text {
  margin-left: 9px;
}

.header__logo-line {
  display: flex;
  align-items: center;
}

.logo-text {
  padding-left: 10px;
}

.logo-text:hover {
  color: #FF9900;
}

.ico {
  cursor: pointer;
  display: inline-block;
}

.ico_play {
  background-image: url("../img/icons/ico-play.svg");
  width: 18px;
  height: 19px;
}

.icon-square {
  width: 50px;
  height: 50px;
  border-radius: 19px;
  display: inline-block;
}
.icon-square_violet {
  background-image: url("../img/tools/1.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
}
.icon-square_yellow {
  background-image: url("../img/tools/2.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
}
.icon-square_red {
  background-image: url("../img/tools/3.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
}
.icon-square_blue {
  background-image: url("../img/tools/4.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
}

.tags {
  display: flex;
}
.tags > * {
  margin-right: 7px;
}

.tag {
  border-radius: 90px;
  padding: 0 14px;
  cursor: pointer;
  font-style: normal;
  font-weight: bold;
  font-size: 1.3rem;
  line-height: 2.8rem;
}
.tag_selected {
  background-color: #FF9900;
}
.tag_bordered {
  border: 1px solid #D7D7D7;
}
.tag_colored {
  background-color: #F5F5F5;
}

.tag:hover {
  background-color: #FF9900;
}

.wrapper {
  max-width: 1197px;
  margin: auto;
}

.header {
  background: #ffffff;
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.05);
  padding: 15px 0 17px 0;
}

.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.layout-4-column {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.layout-4-column > * {
  flex-basis: 21%;
}

.layout-3-column {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.layout-3-column > * {
  flex-basis: 30%;
}

.layout-2-column {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.layout-2-column > * {
  flex-basis: 45%;
}

.tools__wrapper {
  padding-bottom: 120px;
  background-image: url("../img/tools/tools-bg.svg");
  background-position: center bottom;
  background-size: 100%;
  background-repeat: no-repeat;
}

.tools__title {
  max-width: 462px;
  margin: auto;
  text-align: center;
  margin-bottom: 70px;
}

.tools__button {
  margin-top: 60px;
  text-align: center;
}

.tool__title {
  margin: 30px 0 16px;
}

.strategies {
  padding: 30px 0;
}

.strategies__title {
  margin: 0 0 14px;
}

.strategies__description {
  margin-bottom: 0 0 24px;
}

.strategies__tags {
  margin-bottom: 30px;
}

.strategy__image {
  width: 100%;
}

.strategy-wrapper > * {
  margin-bottom: 30px;
}

.strategy__content {
  padding: 24px 33px 26px;
}

.strategy__name {
  margin: 0 0 29px;
}

.promo {
  margin-bottom: 110px;
}

.promo__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.promo__content {
  padding-right: 65px;
}

.promo__img {
  margin-top: 30px;
}

.promo__img-pic {
  max-width: 632px;
}

.promo__title {
  margin: 0 0 30px;
}

.promo__buttons > * {
  margin-right: 44px;
}

.promo__text {
  max-width: 487px;
  margin: 0 0 45px;
  font-style: normal;
  font-weight: normal;
  font-size: 1.8rem;
  line-height: 2.8rem;
}

.navigation {
  display: flex;
  font-size: 1.5rem;
  line-height: 2.8rem;
}

.navigation__link {
  list-style: none;
  text-decoration: none;
  color: #544837;
  padding: 0 10px;
  cursor: pointer;
}
.navigation__link:hover {
  color: #FF9900;
}

